import React, { Component } from 'react';
import Flootsheetio from './Flootsheetio'
// import Flowsheetvitalsign from './Flootsheetvitalsign'
import Vitalxaxis from '../../Module/Vitalxaxis'
import { DatePicker, Button } from 'antd';
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import './Flootsheetlayout.scss'
import X2JS from 'x2js';
import * as Wardstate from '../../Function/Wardstate'
import locale from 'antd/es/date-picker/locale/zh_TW';

class Flowsheetlayout extends Component {
    state = {
        PickerDate: moment(moment().format('YYYYMMDD')).valueOf(),
        preCID: null,
        QSPE: [],
        // itemlist: []
    }
    componentDidMount() {
        const { caseinfo } = this.props
        const CID = caseinfo.CASEID
        const date = moment(moment().format('YYYYMMDD')).valueOf()
        this.setState({ preCID: CID, PickerDate: date })
        this.ApiData(CID, date)
    }
    componentDidUpdate() {
        const CID = this.props.caseinfo.CASEID
        const preCID = this.state.preCID
        if (CID !== preCID) {
            const date = this.state.PickerDate
            this.setState({ preCID: CID, PickerDate: date })
            this.ApiData(CID, date)
        }
    }
    ApiData(CID, DateTime) {
        const st = moment(DateTime + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const et = moment(DateTime + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        ApiProvider.getQSPE(CID, st, et)
            .then(data => {
                const dataJson = Wardstate.XM2JSON(data.data)
                // const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                if (Array.isArray(dataJson)) {
                    this.setState({ QSPE: dataJson })
                }
                else {
                    this.setState({
                        QSPE: [dataJson]
                    })
                }
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
    }
    onDateChange = (date, dateString) => {
        const CID = this.props.caseinfo.CASEID
        this.setState({ preCID: CID, PickerDate: date.valueOf() })
        this.ApiData(date.valueOf())

    }

    trainDay = (e) => {
        const { caseinfo } = this.props
        const { PickerDate } = this.state
        const bron = caseinfo.PBASINGNINFO.PBIRTHDT
        const bronDate = moment(moment(bron).format('YYYYMMDD')).valueOf()

        const day = 24 * 60 * 60 * 1000

        switch (e.target.value) {
            case 'B':
                const bTime = PickerDate - day
                const bTemp = bTime - (6 * day)
                bronDate <= bTemp && this.onDateChange(moment(bTime))
                break;
            case 'N':
                const nTime = moment(moment().format('YYYYMMDD'))
                this.onDateChange(nTime)
                break;
            case 'A':
                const aTime = PickerDate + day
                const aTemp = moment(moment().format('YYYYMMDD')).valueOf()
                aTime <= aTemp && this.onDateChange(moment(aTime))
                break;
            default:
                break;
        }
    }

    render() {
        const { caseinfo } = this.props
        const data = this.state.QSPE || [] //特殊護理事件
        const splist = data.filter(function (value, index, array) {
            if (value === undefined) {
                return null
            }
            console.log(value)
            const str = value.special
            return str.match("@v") === null
        })
        const specialListRender = splist.map(function (value, index, arr) {
            return (
                <div key={index} style={{ display: "flex", color: "rgba(59, 151, 225, 1)" }}>{value.special.toString().trim()}</div>
            );
        })
        var timeArray = []
        for (let index = 7; index < 31; index++) {
            const num = parseInt(index % 24)
            timeArray.push(num)
        }
        const timeCellRender = timeArray.map(function (info, index, arr) {
            return (
                <div key={index} style={{ borderLeft: index === 0 ? "3px solid white" : "2px solid white", display: "flex", justifyContent: "center", alignItems: "center" }}>{info}</div>
            );
        })
        console.log(timeArray)
        return (
            <div>
                <div style={{ display: "flex" }}>
                    <DatePicker disabledDate={(current) => { return current > moment().startOf("day") || current < moment(caseinfo.PBASINGNINFO.PBIRTHDT.toString(), "YYYYMMDD").endOf() }} width={120} value={moment(this.state.PickerDate)} allowClear={false} onChange={this.onDateChange} />
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gridColumnGap: "1px", padding: "2px 10px 0px 10px" }}>
                        <Button value="B" shape="circle" icon="left-circle" onClick={this.trainDay} />
                        <Button value="N" shape="circle" icon="pause-circle" onClick={this.trainDay} />
                        <Button value="A" shape="circle" icon="right-circle" onClick={this.trainDay} />
                    </div>
                </div>
                <div style={{ backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>I/O</t2>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "14px", marginTop: "10px", color: "rgba(0,0,0,.6)", fontSize: "14px" }}>
                    <div style={{ borderRight: "3px solid white", display: "flex", justifyContent: "center", alignItems: "center" }}>{"時間"}</div>
                    {timeCellRender}
                </div>
                <div className='flootsheetlayout'>
                    <Flootsheetio PickerDate={this.state.PickerDate} caseinfo={caseinfo}></Flootsheetio>
                </div>
            </div>
        );
    }
}

export default Flowsheetlayout;
